.loader {
  width: 164px;
  height: 164px;
  border-radius: 50%;
  animation: rotate .75s linear infinite;
  position: relative;
}

.loader:before {
  content: "";
  width: 20px;
  height: 40px;
  box-sizing: border-box;
  background-image: linear-gradient(#1a1b6b 20px, rgba(0, 0, 0, 0) 0), linear-gradient(#1a1b6b 30px, rgba(0, 0, 0, 0) 0), linear-gradient(#1a1b6b 30px, rgba(0, 0, 0, 0) 0);
  background-position: center, 12px 0, 3px 0;
  background-repeat: no-repeat;
  background-size: 10px 12px, 1px 30px, 1px 30px;
  border: 2px solid #1a1b6b;
  border-width: 12px 2px 7px;
  border-radius: 2px 2px 1px 1px;
  position: absolute;
  transform: rotate(45deg)translate(24px, -10px);
}

.loader:after {
  content: "";
  height: 4px;
  width: 4px;
  color: #ebdc0a;
  border-radius: 50%;
  position: absolute;
  top: 47px;
  left: 20px;
  box-shadow: -4px 7px 2px, -7px 16px 3px 1px, -11px 24px 4px 1px, -6px 24px 4px 1px, -14px 35px 6px 2px, -5px 36px 8px 2px, -5px 45px 8px 2px, -14px 49px 8px 2px, 6px 60px 11px 1px, -11px 66px 11px 1px, 11px 75px 13px, -1px 82px 15px;
}

@keyframes rotate {
  to {
    transform: rotate(360deg);
  }
}

